const { Component } = React

const BigNumber = ({ children }) => 
  <div className="big-number">
    {children}
    <style jsx>{`
      .big-number {
        font-family: Helvetica;
        font-size: 20rem;
        color: white;
      }
    `}</style>
  </div>

class Page extends Component {
  render(){
    return ( 
      <div>
        <BigNumber>0</BigNumber>
        <style jsx>{`
          
          div {
            background: #000;
            height: 100vh;
            width: 100vw;
            display: flex;
            justify-content;
            align-items: center;
          }
          
        `}</style>
      </div>
    )
  }
}

export default Page



